.myImg {
  height: 23rem;
  width: 20rem;
  border-radius: 4px;
  border: 1px solid black;
}

.match {
  animation: love 2s ease-out;
  color: #ff5864;
  text-shadow: 1px 1px black;
  font-weight: bolder;
  font-size: 1rem;
}

@keyframes love {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    transform: scale(2) translateY(-150px);
  }
  100% {
    opacity: 1;
    transform: scale(2) translateY(-150px);
  }
}
