* {
  font-family: "Exo 2", sans-serif;
}
.App {
  text-align: center;
}
body {
  background-image: url("./img/heart.jpg");
  background-position: center;
  background-position: cover;
}

.title {
  color: hsl(356, 100%, 67%);
  text-shadow: 1px 1px black;
}

.dislikeBtn {
  background-color: hsl(0, 0%, 100%, 0);
  border: none;
  font-size: 3rem;
  cursor: pointer;
}

.likeBtn {
  color: red;
  background-color: hsl(0, 0%, 100%, 0);
  border: none;
  font-size: 3.5rem;
  cursor: pointer;
}
